import React from "react";
import s from "./Users.module.css";
import userpng from "../../assets/images/userpng.jpg";
import {NavLink} from "react-router-dom";

let User = ({user, followingInProgress, unfollowThunk, followThunk}) => {

    return (<div className={s.object} key={user.id}>

                    <span className={s.left}>
                        <div>
                            <NavLink to={'/profile/' + user.id} className={s.avatar}>
                            <img src={user.photos.small != null ? user.photos.small : userpng} className={s.avatar}/>
                            </NavLink>
                        </div>
                        <div className={s.user}>
                            {user.followed
                                ? <button disabled={followingInProgress.some(id => id === user.id)}
                                          className={s.button1}
                                          onClick={() => {
                                              unfollowThunk(user.id)
                                          }}> Unfollow </button>
                                : <button disabled={followingInProgress.some(id => id === user.id)}
                                          className={s.button}
                                          onClick={() => {
                                              followThunk(user.id)
                                          }}>Follow </button>}
                        </div>
                    </span>
            <span className={s.right}>
                <div className={s.name}>{user.name}</div>
                <div className={s.idNumber}>Мой айди: #{user.id}</div>
                <div className={s.isFriend}>{user.followed ? 'ты мой друг' : 'ты мне не друг'}</div>
                <div className={s.status}> {user.status}</div>
            </span>

        </div>

    )


}
export default User