import React from "react";
import s from "./Paginator.module.css"

const Paginator = ({totalUsersCount,pageSize,activePage,onPageChange,}) => {

    let pagesCount = Math.ceil(totalUsersCount / pageSize)
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    return <div>

        <span className={s.h1}> Users
            </span>
        <div className={s.pageNumbers}>
            {pages.map(p => {
                return <span className={activePage === p ? s.selectedPage : s.pageNumber}
                             onClick={(e) => {
                                 onPageChange(p);
                             }}>{p} </span>
            })}
        </div>
    </div>


}
export default Paginator