import React from "react"
import {connect} from "react-redux";
import {
    toggleFollow,
    setActivePage,
    getUsers, unfollowThunk, followThunk
} from "../../redux/usersReducer";
import Users from "./Users";
import {API} from "../API/Api";
import {toggleFollowingInProgress} from "../../redux/usersReducer";
import {compose} from "redux";
import {usersReselector} from "../../redux/usersReselectors";

class UsersAPI extends React.Component {
    componentDidMount() {
        let {activePage, pageSize,getUsers} = this.props
        getUsers(activePage, pageSize)
    }


    onPageChange = (pageNumber) => {
        let {setActivePage, getUsers,activePage,pageSize} = this.props
       setActivePage(pageNumber);
       getUsers(activePage,pageSize)
    }

    render() {
        return <>
            <Users activePage={this.props.activePage}
                   users={this.props.users}
                   toggleFollow={this.props.toggleFollow}
                   onPageChange={this.onPageChange}
                   pageSize={this.props.pageSize}
                   totalUsersCount={this.props.totalUsersCount}
                   isFetching={this.props.isFetching}
                   API={API}
                   followingInProgress={this.props.followingInProgress}
                   unfollowThunk={this.props.unfollowThunk}
                   followThunk={this.props.followThunk}

            />

        </>
    }
}

let mapStateToProps = (state) => {
    return {
        users: usersReselector(state),
        pageSize: state.usersPage.pageSize,
        totalUsersCount: state.usersPage.totalUsersCount,
        activePage: state.usersPage.activePage,
        isFetching: state.usersPage.isFetching,
        followingInProgress: state.usersPage.followingInProgress,
    }
}


export default compose(connect(mapStateToProps,
    {toggleFollow, setActivePage, toggleFollowingInProgress, getUsers, unfollowThunk, followThunk}
))
(UsersAPI);